:root {

	--driveschol-font: 'Manrope', sans-serif;
	--driveschol-heading-font: 'Urbanist', sans-serif;
	--driveschol-special-font: 'Alex Brush', cursive;
	--driveschol-fontawesome: 'Font Awesome 5 free';


	--driveschol-primary: #EC2526;
	--driveschol-primary-rgb:#{toRGB(#EC2526)};


	--driveschol-text: #6D7076;
	--driveschol-text-rgb: #{toRGB(#6D7076)};

	--driveschol-heading-text: #111117;
	--driveschol-heading-text-rgb: #{toRGB(#111117)};

	--driveschol-text-dark: #6e6b70;
	--driveschol-text-dark-rgb: #{toRGB(#6e6b70)};
	--driveschol-text-gray: #89868d;
	--driveschol-text-gray-rgb: #{toRGB(#89868d)};


	--driveschol-gray: #6D7076;
	--driveschol-gray-rgb: #{toRGB(#6D7076)};
	--driveschol-gray2: #F5F5F5;
	--driveschol-gray2-rgb: #{toRGB(#F5F5F5)};
	--driveschol-gray3: #9D9D9D;
	--driveschol-gray3-rgb: #{toRGB(#9D9D9D)};


	--driveschol-white: #fff;
	--driveschol-white-rgb: #{toRGB(#fff)};


	--driveschol-black: #000;
	--driveschol-black-rgb: #{toRGB(#000)};
	--driveschol-black2: #111117;
	--driveschol-black2-rgb: #{toRGB(#111117)};
	--driveschol-black3: #2A2A30;
	--driveschol-black3-rgb: #{toRGB(#2A2A30)};



	--driveschol-border-color: #E6E6E6;
	--driveschol-border-color-rgb: #{toRGB(#E6E6E6)};
	--driveschol-border-color2: #DA0D0E;
	--driveschol-border-color2-rgb: #{toRGB(#DA0D0E)};
	--driveschol-border-color3: #373745;
	--driveschol-border-color4: #D61819;


	--driveschol-letter-space: 0.1em;
	--driveschol-letter-space-xl: 0.2em;
}


$driveschol-font: var(--driveschol-font, 'Manrope', sans-serif);
$driveschol-heading-font: var(--driveschol-heading-font, 'Urbanist', sans-serif);
$driveschol-special-font: var(--driveschol-special-font, 'Alex Brush', cursive);
$driveschol-fontawesome: var(--fontawesome, 'Font Awesome 5 free');



$driveschol-primary:var(--driveschol-primary, #EC2526);
$driveschol-primary-rgb:var(--driveschol-primary-rgb, #{toRGB(#EC2526)});



$driveschol-text: var(--driveschol-text, #6D7076);
$driveschol-text-rgb: var(--driveschol-text-rgb, #{toRGB(#6D7076)});
$driveschol-heading-text: var(--driveschol-heading-text, #111117);
$driveschol-heading-text-rgb: var(--driveschol-heading-text-rgb, #{toRGB(#111117)});


$driveschol-text-dark:var(--driveschol-text-dark, #6e6b70);
$driveschol-text-dark-rgb:var(--driveschol-text-dark-rgb, #{toRGB(#6e6b70)});
$driveschol-text-gray:var(--driveschol-text-gray, #89868d);
$driveschol-text-gray-rgb:var(--driveschol-text-gray-rgb, #{toRGB(#89868d)});


$driveschol-gray:var(--driveschol-gray, #6D7076);
$driveschol-gray-rgb:var(--driveschol-gray-rgb, #{toRGB(#6D7076)});
$driveschol-gray2:var(--driveschol-gray2, #F5F5F5);
$driveschol-gray2-rgb:var(--driveschol-gray2-rgb, #{toRGB(#F5F5F5)});
$driveschol-gray3:var(--driveschol-gray3, #9D9D9D);
$driveschol-gray3-rgb:var(--driveschol-gray3-rgb, #{toRGB(#9D9D9D)});


$driveschol-white:var(--driveschol-white, #fff);
$driveschol-white-rgb:var(--driveschol-white-rgb, #{toRGB(#fff)});


$driveschol-black:var(--driveschol-black, #000);
$driveschol-black-rgb:var(--driveschol-black-rgb, #{toRGB(#000)});
$driveschol-black2:var(--driveschol-black2, #111117);
$driveschol-black2-rgb:var(--driveschol-black2-rgb, #{toRGB(#111117)});
$driveschol-black3:var(--driveschol-black3, #2A2A30);
$driveschol-black3-rgb:var(--driveschol-black3-rgb, #{toRGB(#2A2A30)});



$driveschol-border-color:var(--driveschol-border-color, #E6E6E6);
$driveschol-border-color-rgb:var(--driveschol-border-color-rgb, #{toRGB(#E6E6E6)});
$driveschol-border-color2:var(--driveschol-border-color2, #DA0D0E);
$driveschol-border-color2-rgb:var(--driveschol-border-color2-rgb, #{toRGB(#DA0D0E)});
$driveschol-border-color3:var(--driveschol-border-color3, #373745);
$driveschol-border-color4:var(--driveschol-border-color4, #D61819);


$driveschol-letter-space:var(--driveschol-letter-space, 0.1em);
$driveschol-letter-space-xl:var(--driveschol-letter-space-xl, 0.2em);


$xxxl: 'only screen and (min-width: 1601px) and (max-width: 1700px)';
$xxl: 'only screen and (min-width: 1400px) and (max-width: 1600px)';
$xl: 'only screen and (min-width: 1200px) and (max-width: 1399px)';
$lg : 'only screen and (min-width: 992px) and (max-width: 1199px)';
$md:'only screen and (min-width: 768px) and (max-width: 991px)';
$sm: 'only screen and (min-width: 576px) and (max-width: 767px)';
$xs:'(max-width: 575px)';